.menu {
  background: #c2c2c2;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &__top {
    @include top-actions;
    @include top-actions--menu;
    
    margin-bottom: 44px;

    @include onDesktop {
      margin-bottom: 48px;
    }
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: 600;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $darkColor;
    text-decoration: none;
    transition: border-color 0.3s;

    &:hover {
      border-bottom: 1px solid $darkColor;
    }

    @include onDesktop {
      font-weight: 700;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: 600;
    text-transform: uppercase;
    color: $darkColor;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 27px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid $darkColor;
    }

    @include onDesktop {
      font-weight: 700;
    }
  }
}
