.about {
  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;
    color: $whiteColor;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 60px;
    }
  }

  &__paragraph {
    margin: 0;
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
    color: $whiteColor;
  }
}
