.page {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: $whiteColor;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background: $darkColor;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 92px;
    
    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesktop {
      margin-bottom: 160px;
    }
  }

  &__section-title {
    margin: 0 0 48px;
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    text-align: center;
    letter-spacing: -1.5px;
    text-transform: capitalize;

    @include onTablet {
      margin-bottom: 64px;
    }
  }
}
